.link {
    text-decoration: none;
    color: black;
  }
  
  .titleContainer {
    width: 1240px;
    height: 100px;
    display: flex;
    align-items: center;
    margin: 80px 0 0 250px;
  }
  
  .titleContainer h1 {
    color: #004C59;
    font-size: 30px;    
  }
  
  .tableContainer {
    max-width: 1240px;
    margin: 10px 0 0 250px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 8px;
  }
  
  .tableContainer table {
    width: 100%;
    border-collapse: collapse;
    background: white;
  }
  
  .tableContainer th {
    text-align: left;
    padding: 12px 16px;
    color: #666;
    border-bottom: 1px solid #eee;
    font-weight: 500;
  }
  
  .tableContainer td {
    padding: 12px 16px;
    border-bottom: 1px solid #eee;
  }
  
  .tableContainer tr:hover {
    background-color: #f8f9fa;
  }
  
  .thIcon {
    margin-left: 5px;
    color: #999;
    cursor: pointer;
  }
  
  .status {
    display: inline-flex;
    align-items: center;
    gap: 6px;
  }
  
  .statusDot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }
  
  .statusActive {
    background-color: #22c55e;
  }
  
  .statusBlocked {
    background-color: #ef4444;
  }
  
  .statusLocked {
    background-color: #64748b;
  }
  
  .actionButtons {
    display: flex;
    gap: 8px;
  }
  
  .btn {
    padding: 6px;
    border: 1px solid #e5e7eb;
    border-radius: 6px;
    background: white;
    cursor: pointer;
  }
  
  .btn:hover {
    background: #f8f9fa;
  }
  
  .pagination {
    display: flex;
    gap: 4px;
    margin-top: 16px;
    align-items: center;
  }
  
  .pageItem {
    padding: 8px 12px;
    border: 1px solid #e5e7eb;
    border-radius: 6px;
    cursor: pointer;
  }
  
  .pageItemActive {
    background: #2563eb;
    color: white;
    border-color: #2563eb;
  }
  
  .itemsPerPage {
    margin-left: 16px;
    padding: 8px;
    border: 1px solid #e5e7eb;
    border-radius: 6px;
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
  }
    
  .modalContent {
    background: #fff;
    padding: 20px;
    border-radius: 12px;
    min-width: 300px;
    box-shadow: 0 5px 20px rgba(0,0,0,0.2);
  }
    
  .modalContent h3 {
    margin-bottom: 15px;
  }
    
  .modalContent label {
    display: block;
    margin-bottom: 10px;
  }
    
  .modalContent select {
    width: 100%;
    padding: 8px;
    margin-top: 4px;
  }
    
  .modalActions {
    text-align: right;
    margin-top: 20px;
  }
    
  .modalActions button {
    padding: 6px 12px;
    margin-left: 8px;
  }
  